import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {createUpdateStateEffect} from '@servicenow/ui-effect-update-state';

const loadEmailDetailsQuery = `
query ($queryConditions: String!) {
  GlideRecord_Query {
    task(queryConditions: $queryConditions) {
      _results {
        number {
          value
        }
      }
    }
  }
}
`

const loadTabTitleEffect = createGraphQLEffect (loadEmailDetailsQuery, {
    successActionType: 'EMAIL_TAB_TITLE_SUCCESS',
    errorActionType: 'EMAIL_TAB_TITLE_ERROR',
    variableList: ['queryConditions']
});

const loadTabTitleSuccessEffect = createUpdateStateEffect((state, action) => {
    const data = action.payload.data;
    const caseNumber = data.GlideRecord_Query.task._results[0].number.value;
    return{
        ...state,
        updateTitle: true,
        tabTitleValue: caseNumber + " - " + state.properties.params.headerValue

    }
});


export default {
    ['LOAD_TAB_TITLE']: loadTabTitleEffect,
    ['EMAIL_TAB_TITLE_SUCCESS']: loadTabTitleSuccessEffect,
    ['EMAIL_TAB_TITLE_ERROR'] : () => {
        console.log('Unable to get task number');  // TO-DO
    }
}