import '@servicenow/now-heading';
import '@servicenow/now-record-form-connected';

var iframeRef='';

const closeTab = (state, dispatch) => {
	const {tabId} = state.properties;
	const payload = {
		type: 'UPDATE_ONE_TAB_CLOSE',
		tab_id: tabId
	}
	dispatch('UPDATE_ONE_TAB', payload)
}

const updateTabTitle = (state, dispatch, subjectValue) => {
	if(state.updateTitle){
		const payload = {
			type: 'UPDATE_ONE_TAB_TITLE',
			tab_id: state.properties.tabId,
			title: state.tabTitleValue + ' - ' + subjectValue
		}
		setTimeout(() => {
			dispatch('UPDATE_ONE_TAB', payload)
		})
	}
}

const onLoadIframe = (state, dispatch) => {
	if(iframeRef.contentWindow) {
		iframeRef.contentWindow.addEventListener ('unload', () => {
			closeTab(state, dispatch)
		})
		var texAreaHeight = iframeRef.contentWindow.document.getElementsByTagName('html')[0].querySelector('iframe').offsetHeight;
		var adjustedHeight = texAreaHeight - 5;
		iframeRef.contentWindow.document.getElementsByTagName('html')[0].querySelector('iframe').style.height = adjustedHeight + 'px';

		var subjectValue = iframeRef.contentWindow.document.getElementById('subject').value;
		updateTabTitle(state, dispatch, subjectValue)
	}
}

export const view = (state, {dispatch}) => {
	const {sysId, replyId, replyType, params} = state.properties;
	const { parentTable, parentSysId, headerValue} = params;
	var emailSrc = `/email_client.do?sysparm_table=${parentTable}&sysparm_sys_id=${parentSysId}&sysparm_target=${parentTable}&sys_target=${parentTable}&sys_uniqueValue=${parentSysId}&sys_row=0&sysparm_encoded_record=&sysparm_domain_restore=false&sysparm_stack=no`;

	if(replyType)
		emailSrc = emailSrc.concat(`&replytype=${replyType}&replyid=${replyId}`)
	else if(sysId != '-1' && sysId)
		emailSrc = emailSrc.concat(`&sysparm_email_id=${sysId}`)
	
	const heading = state.tabTitleValue ? state.tabTitleValue : headerValue;

	return (
		!headerValue ? (
			<now-record-form-connected
				table='sys_email'
				sys-id={sysId}
				view='workspace'
				workspace-config-id='7b24ceae5304130084acddeeff7b12a3'
			/>
		) : headerValue && state.updateTitle ? (
				<div className="mainContainer">
					<now-heading label={heading} wont-wrap />
					<div className="newEmailTab">
						<iframe
							className="iframeModal"
							src={emailSrc}
							ref = {(e) => iframeRef = e}
							on-load = {() => onLoadIframe(state, dispatch)}
						/>
					</div>
				</div>
			) : ''
	);
};