import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './snEmailContentComponent'
import actionHandlers from './snEmailContentAction'
import styles from './_snEmailContentComponent.scss'


createCustomElement('sn-email-content',{
    renderer: {
        type: snabbdom,
        view
    },
    properties: {
        sysId: '',
        table: '',
        fields: ''
    },
    initialState: {
        emailData: ''
    },
    actionHandlers,
    styles: [styles],
    onBootstrap(host, dispatch) {
		const sysId = host.sysId;
		dispatch('LOAD_EMAIL_DETAILS', {
			sysId: sysId
		})
	}
})