import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {createUpdateStateEffect} from '@servicenow/ui-effect-update-state';

const loadEmailDetailsQuery = `
query ($sysId: String!) {
    GlideRecord_Query {
      sys_email(sys_id: $sysId) {
        _results {
          body_text {
            htmlValue
          }
          subject {
            value
          }
          recipients {
            value
          }
          copied {
            value
          }
          user_id {
            _reference{
              name {
                value
              }
              avatar {
                value
              }
            }
            value
          }
          sys_created_on {
            value
          }
        }
      }
    }
  }
  `

const loadEmailDetailsEffect = createGraphQLEffect (loadEmailDetailsQuery, {
    successActionType: 'EMAIL_DETAILS_SUCCESS',
    errorActionType: 'EMAIL_DETAILS_ERROR',
    variableList: ['sysId']
});

const emailDetailsSuccessEffect = createUpdateStateEffect ((state, action) => {
    const emailData = action.payload.data.GlideRecord_Query.sys_email._results[0]
    return {
        ...state,
        emailData
    }
})


export default {
    ['LOAD_EMAIL_DETAILS']: loadEmailDetailsEffect,
    ['EMAIL_DETAILS_SUCCESS']: emailDetailsSuccessEffect,
    ['EMAIL_DETAILS_ERROR'] : () => {
        console.log('Unable to load email details');  // TO-DO
    },
    ['NOW_BUTTON#CLICKED']: (({state,dispatch}) => {
      const payload = {
        table: 'sys_email',
        sys_id: '-1',
        params: {
          parentTable: state.properties.fields.instance.reference,
          parentSysId: state.properties.fields.instance.value,
          replyId: state.properties.sysId,
          replyType: 'reply',
          headerValue: state.emailData.subject.value
        }
      };
      dispatch('PREVIEW_RECORD', payload);
    })
}