import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './_snEmailEditor.scss';
import {view} from './snEmailEditorComponent';
import actionHandlers from './snEmailEditorAction';


createCustomElement('sn-email-editor', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		updateTitle: false,
		tabTitleValue: ''
	},
	properties: {
		table: {default: ''},
		sysId: {default: ''},
		parentTable: {default: ''},
		parentSysId: {default: ''},
		replyId: {default: ''},
		replyType: {default: ''},
		params: {default: '', schema: {type: 'object'}},
		tabId: {default: ''}
	},
	actionHandlers,
	styles: [styles],
	onBootstrap(host, dispatch) {
		const query = "sys_class_name=" + host.params.parentTable + "^sys_id=" + host.params.parentSysId;
		dispatch('LOAD_TAB_TITLE', {
			queryConditions: query
		})
	}
});
