import "@servicenow/now-avatar";
import "@servicenow/now-button";
import "@servicenow/now-label-value";
import { t } from 'sn-translate';

const view = (state, dispatch) => {
    const {subject, copied, recipients, sys_created_on} = state.emailData;
    const user = state.emailData.user_id._reference;

    const headerData = [
        {
            label: t('Subject'),
            value: subject.value
        },
        {
            label: t('To'),
            value: recipients.value ? recipients.value.split(',').join('; '): ''
        },
        {
            label: t('CC'),
            value: copied.value ? copied.value.split(',').join('; '): ''
        }
    ]

    const hideReplyBtn = ( state.properties.fields.type.value === 'send-ignored' || 
                            state.properties.fields.type.value === 'send-failed' ||
                            state.properties.fields.type.value === 'received-ignored') ? true : 
                            false;
    return (
        state.emailData ? (
            <div className="emailContentContainer">
                <div className="header">
                    <now-avatar 
                        size="md" 
                        image-src={user.avatar.value ? user.avatar.value  + ".iix": ''} 
                        user-name={user.name.value}
                    />
                    <div className="headerContent">
                        <div className="authorName"> 
                            {user.name.value} 
                        </div>
                        <div className="createdDate"> 
                            <time>
                                {sys_created_on.value}
                            </time> 
                        </div>
                    </div>
                    {
                        hideReplyBtn ? '' :
                            <now-button
                                className="replyButton"
                                label={ t('Reply') }
                                size="md"
                            />
                    }
                </div>
                
                <now-label-value-tabbed
                    size="md"
                    items={headerData}
                />

                <div 
                    className="emailBody"
                    innerHTML={state.properties.fields.body.displayValue}>
                </div>
            </div>
        ) : ''
        
    )
}

export default view;